@import url(https://fonts.googleapis.com/css2?family=Ms+Madi&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



nav {
  display: flex;
  justify-content: space-between;
  padding: 1em;
  box-shadow: 0.1px 0px 0.7px lightgrey;
  background-color: rgb(249, 26, 63);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.name-span {
  font-family: "Ms Madi", cursive;
  font-weight: 900;
  font-size: xx-large;
  display: flex;
  align-items: center;
}
.nav-links {
  width: 43%;
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}
.nav-links > li > a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}
.reservation-span {
  border: none;
  background-color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2em;
  width: 10em;
  padding: 1em 1.4em;
  box-sizing: content-box;
}
.reservation-span > a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  transition: all 0.3s ease;
}
.reservation-span > a:hover {
  color: rgb(249, 26, 63);
}
.icons-container {
  display: flex;
  flex-grow: 0.2;
  justify-content: space-around;
}
.social-icons {
  display: flex;
  align-items: center;
  justify-content: center;
}
.social-icons > a {
  transition: all 0.3s ease;
}
.social-icons > a:hover {
  color: white;
}

.home-container {
  min-height: 100vh;
  width: 100%;
  margin-left: 0;
  height: auto;
  background-image: url(/restaurant-project2/static/media/victoria-shes-UC0HZdUitWY-unsplash.8ccacd6c.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home-container > h1,
.home-container > h1 > p {
  color: white;
}
.home-container > h1 {
  font-family: cursive;
  text-align: center;
}

.about-container {
  background-color: black;
  color: white;
}
.about {
  margin: 0;
  text-align: center;
}
.soup {
  width: 500px;
  height: 500px;
}
.rem {
  background-image: url(/restaurant-project2/static/media/interior.0cd9fdcf.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: space-around;
  padding: 2em;
  border-top: 2px solid lightcoral;
}
.text-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form-el {
  width: 100%;
  margin: auto;
}
.contact-form {
  background-color: black;
  color: white;
  padding: 50px;
}
.contact-form > input {
  width: 100%;
  background-color: black;
  color: white;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid white;
  outline: none;
}
.contact-form .submit {
  width: 100%;
  border-radius: 15px 50px;
  background-color: red;
  color: black;
  font-weight: bold;
  border: none;
  padding: 16px 32px;
  text-decoration: none;
  margin: auto;
  cursor: pointer;
}
.ResForm {
  width: 100%;
  padding: 15px 30px;
}
.form-el2 {
  width: 30%;
  padding: 12px 20px;
  margin: 8px 10px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid red;
  outline: none;
}
.people {
  width: 30%;
  padding: 12px 20px;
  margin: 8px 10px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid red;
  outline: none;
}
.ResForm .submit {
  width: 100%;
  border-radius: 15px 50px;
  background-color: red;
  color: black;
  font-weight: bold;
  border: none;
  padding: 16px 32px;
  text-decoration: none;
  margin: 10px;
  cursor: pointer;
}
.submit:hover {
  background-color: aqua;
}
.contact-container {
  height: 100%;
  width: 100%;
  margin-left: 0;
  background-image: url(/restaurant-project2/static/media/gold-cutlery-set-black-background.59c1a98a.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}
.contact-container > h1 {
  font-family: cursive;
  text-align: center;
  color: white;
  font-size: 60px;
  font-weight: bold;
}
.section-el {
  text-align: center;
  margin-top: 60px;
  margin-bottom: 20px;
  margin-left: 150px;
  margin-right: 150px;
}
.section-el > h5 {
  color: red;
}
.section-el > h2 {
  font-size: 50px;
  margin: 40px;
}
.section-el > p {
  font-size: 20px;
}
#Image img {
  width: 100%;
  margin: 0;
  height: 63%;
}
#Image1 {
  width: 100%;
  height: 100%;
}
#row-el {
  background-color: rgb(221, 239, 255);
  margin-top: -100x;
  padding: 50px 50px;
}
#row-el2 {
  margin-bottom: -280px;
}
#row-el {
  text-align: center;
}
#row-el > div > h4 {
  margin-bottom: 0.6em;
}

.product {
  color: black;
  font-family: "Courier New", Courier, monospace;
  margin-top: 30px;
}
.menu-items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.products-container > h4 {
  text-align: center;
  margin-bottom: 0;
  margin-top: 0.5em;
}

.product-container {
  padding: 0.4em;
  border-radius: 0.3em;
  margin: 1em;
  box-sizing: content-box;
  width: 300px;
  box-shadow: 1px 1px 1px lightcoral, -1px -1px 1px lightcoral;
}
.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.image-container {
  width: 300px;
  height: 350px;
}
.card-text > h4,
.card-text > p {
  text-align: center;
}
.card-text > p {
  margin-bottom: 0;
}
.card-details {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.card-details > span > a {
  text-align: center;
  text-decoration: none;
  color: rgb(249, 24, 63);
}
.card-details > span > a:hover {
  color: coral;
}

.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: -0.1px 0px 0.7px lightgrey;
  padding: 1rem;
  background-color: rgb(250, 208, 208);
}
.footer-container > p > a {
  text-decoration: none;
  color: black;
}
.list-container {
  width: 40%;
  display: flex;
  justify-content: space-around;
}
.list-container > ul {
  list-style: none;
}
.list-container > ul > li > a {
  text-decoration: none;
  font-size: small;
  font-weight: bold;
  color: black;
}
.footer-details > p {
  color: rgb(249, 26, 63);
  font-weight: 600;
  margin-bottom: 8px;
}
.footer-details > h4 {
  font-size: 1em;
  font-weight: 500;
  color: rgb(31, 44, 49);
}

