.form-el {
  width: 100%;
  margin: auto;
}
.contact-form {
  background-color: black;
  color: white;
  padding: 50px;
}
.contact-form > input {
  width: 100%;
  background-color: black;
  color: white;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid white;
  outline: none;
}
.contact-form .submit {
  width: 100%;
  border-radius: 15px 50px;
  background-color: red;
  color: black;
  font-weight: bold;
  border: none;
  padding: 16px 32px;
  text-decoration: none;
  margin: auto;
  cursor: pointer;
}
.ResForm {
  width: 100%;
  padding: 15px 30px;
}
.form-el2 {
  width: 30%;
  padding: 12px 20px;
  margin: 8px 10px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid red;
  outline: none;
}
.people {
  width: 30%;
  padding: 12px 20px;
  margin: 8px 10px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid red;
  outline: none;
}
.ResForm .submit {
  width: 100%;
  border-radius: 15px 50px;
  background-color: red;
  color: black;
  font-weight: bold;
  border: none;
  padding: 16px 32px;
  text-decoration: none;
  margin: 10px;
  cursor: pointer;
}
.submit:hover {
  background-color: aqua;
}
.contact-container {
  height: 100%;
  width: 100%;
  margin-left: 0;
  background-image: url("./gold-cutlery-set-black-background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.contact-container > h1 {
  font-family: cursive;
  text-align: center;
  color: white;
  font-size: 60px;
  font-weight: bold;
}
.section-el {
  text-align: center;
  margin-top: 60px;
  margin-bottom: 20px;
  margin-left: 150px;
  margin-right: 150px;
}
.section-el > h5 {
  color: red;
}
.section-el > h2 {
  font-size: 50px;
  margin: 40px;
}
.section-el > p {
  font-size: 20px;
}
